.burning-container {
  padding: 50px;
  background: black;

  @media screen and (min-width: 411px) and (max-width: 640px) {
    padding-left: 2rem;
    padding-right: 2rem;
  }

  @media screen and (max-width: 410px) {
    padding-left: 1rem;
    padding-right: 1rem;
  }

  .burning-hero {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4rem;
    margin-bottom: 3rem;

    .scrollbtn-wrapper {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: -6rem;

      @media screen and (max-width: 640px) {
        display: none;
      }
    }

    @media screen and (max-width: 640px) {
      background-image: url(../../assets/images/burning/Male.png);
      background-repeat: no-repeat;
      background-size: contain;
    }

    .page-intro {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 2.2rem;

      @media screen and (max-width: 640px) {
        background-color: rgb(93 154 174 / 80%);
        padding: 1rem;
        border-radius: 1rem;
        border: 1px solid var(--success);
      }
    }

    .hero-img-container {
      width: 50%;

      @media screen and (max-width: 640px) {
        display: none;
      }
    }

    .remaining-time {
      margin-top: 0.8rem;
      color: #5e9bb1;
      font-size: 3rem;
      font-style: normal;

      @media screen and (max-width: 640px) {
        color: #ffffff;
      }
    }

    .remaining-time-sub {
      font-size: 1.5rem;
      font-style: normal;
    }
  }

  .burning-desc {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    font-family: 'mont';

    @media screen and (max-width: 640px) {
      display: block;
      background-image: url(../../assets/images/staking/NiQuiz.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }

    .burning-desc-img-container {
      width: 100%;

      @media screen and (max-width: 640px) {
        display: none;
      }
    }

    .burning-description-container {
      padding-left: 4rem;
      padding-right: 4rem;

      @media screen and (max-width: 640px) {
        background-color: rgb(93 154 174 / 80%);
        padding: 1rem;
        border-radius: 1rem;
        border: 1px solid var(--success);
      }

      h5 {
        font-size: 2rem;
        text-transform: uppercase;

        @media screen and (max-width: 640px) {
          font-size: 1.4rem;
        }

        span: {
          color: var(--danger);
        }
      }

      h6.reward {
        color: var(--danger);
        font-size: 1.4rem;
        text-align: center;
        text-decoration: underline;
        font-style: italic;
        text-transform: uppercase;
      }

      .reward-boost-desc {
        font-size: 1.4rem;
        text-align: center;
        font-style: italic;

        @media screen and (max-width: 640px) {
          color: white;
          font-size: 1.1rem;
        }

        span {
          text-decoration: underline;
        }
      }
    }
  }

  .earned-tickets {
    padding-top: 4rem;
    margin-bottom: 4rem;

    .section-title {
      img {
        transform: rotate(-30deg)
      }

      span {
        font-size: 2.5rem;
      }
    }

    .earned-tickets-table {
      border-collapse: collapse;
      border: 1px solid var(--secondary);
      width: 100%;
      text-align: center;
      font-size: 1.4rem;
      font-style: italic;
      font-family: 'kalam';

      tbody {
        tr {
          td {
            padding: 1rem 2rem;
            border-bottom: 1px solid var(--secondary);
            font-family: 'mont';
          }

          &:last-child {
            td.image-cell {
              img {
                width: 25%;
              }
            }
          }

          td.image-cell {
            img {
              width: 14%;
            }
          }
        }
      }

      th {
        color: var(--primary);
        border-bottom: 1px solid var(--secondary);
        text-transform: uppercase;
        padding: 0.5rem;
      }
    }

    .nft-image {
      cursor: pointer;

      &:hover {
        transform: scale(1.1);
      }
    }

    .earned-ticket-card {
      text-transform: uppercase;
      border: 1px solid var(--secondary);
      padding: 0.6rem;
      border-radius: 0.6rem;
      font-family: 'mont';

      .nft-type {
        font-size: 1rem;
      }
    }
  }

  .reward-calculator {

    .result-container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      max-width: 300px;
      width: 50%;
      margin-left: auto;
      margin-right: auto;

      @media screen and (max-width: 640px) {
        width: 80%;
      }

      img {
        width: 5rem;

        &:last-child {
          transform: rotate(180deg);
        }
      }

      .result {
        font-size: 6rem;
        font-family: 'kalam';
      }
    }

    .loh-tickets-container {
      display: flex;
      flex-direction: column;
      gap: 1.2rem;
      justify-content: center;
      align-items: center;

      .title {
        text-decoration: underline;
        color: var(--primary);
        text-transform: uppercase;
        font-style: italic;
        font-family: 'kalam';
        font-size: 2rem;

        @media screen and (max-width: 640px) {
          font-size: 1.4rem;
        }
      }

      img {
        max-width: 300px;

      }
    }
  }

  .loh-burning {
    text-align: center;

    .inyourwallet {
      font-family: 'yokomoji';
      text-transform: uppercase;
      font-size: 1.8rem;
      color: var(--primary);
      text-align: left;
      margin-top: 1rem;
    }

    .loh-burning-select {
      display: flex;
      h6 {
        font-size: 1.8rem;
        font-style: italic;
        margin-bottom: 0.8rem;
      }

      p {
        font-size: 1.4rem;
        padding: 0.8rem;
        text-transform: uppercase;
        font-weight: 700;

        span {
          color: var(--danger);
        }
      }
    }

    .loh-burning-tabs {
      .react-tabs__tab-list {
        display: flex;
        justify-content: center;

        .react-tabs__tab {
          background-color: transparent;
          font-family: 'mont';

          h6 {
            font-size: 1.8rem;
            font-style: italic;
            margin-bottom: 0.8rem;
          }

          p {
            font-size: 1.4rem;
            padding: 0.8rem;
            text-transform: uppercase;
            font-weight: 700;

            span {
              color: var(--danger);
            }
          }

          &.react-tabs__tab--selected {
            border: none;

            h6 {
              color: var(--primary);
            }

            p {
              background-color: var(--primary-1);
            }

            &::after {
              display: none;
            }
          }
        }
      }

      .table-wrapper {
        max-height: 100vh;
        overflow: auto;
        border-bottom: 1px solid var(--secondary);

        table {
          border-collapse: collapse;
          border: 1px solid var(--secondary);
          border-bottom: none;
          width: 100%;
          font-family: 'mont';

          thead {
            tr {
              th:first-child {
                max-width: 50px;
                width: 5%;
              }
            }
          }

          th,
          td {
            text-transform: uppercase;
            color: var(--secondary);
            font-style: italic;
            font-size: 1.4rem;
            border-bottom: 1px solid var(--secondary);
          }

          tbody {
            tr:last-child {

              td,
              th {
                border-bottom: none;
              }
            }
          }

          th {
            padding: 0.5rem;
          }

          td {
            &.percent {
              span {
                font-size: 1rem;
              }
            }

            &:first-child {
              border: none;
            }

            .auto-restake-btn {
              background-color: transparent;
              border: none;
              outline: none;
              width: 50px;
              padding: 0;
            }
          }

          .nft-visual {
            img {
              max-width: 10rem;
            }
          }
        }
      }

      .cards-wrapper {
        max-height: 100vh;
        overflow: auto;
      }

      .loh-burning-card {
        position: relative;
        display: flex;
        align-items: center;
        gap: 1rem;
        border: 1px solid var(--secondary);
        border-radius: 1rem;
        padding: 1rem;
        text-align: left;
        margin-bottom: 1rem;
        font-family: 'mont';

        .img-container {
          width: 100%;
        }

        .loh-checkbox {
          input+label {
            width: 21px;
            height: 20px;
          }

          position: absolute;
          bottom: 0.5rem;
          right: 1rem;
        }
      }

      .loh-burned-card {
        position: relative;
        display: flex;
        gap: 0.5rem;
        padding: 0.5rem;
        align-items: center;
        margin-bottom: 1rem;
        border: 1px solid var(--secondary);
        border-radius: 1rem;
        text-align: left;

        .img-container {
          width: 100%;
        }

        .loh-checkbox {
          input+label {
            width: 21px;
            height: 20px;
          }

          position: absolute;
          bottom: 0.5rem;
          right: 1rem;
        }

        .auto-restake-btn {
          background-color: transparent;
          border: none;
          outline: none;
          width: 20px;
          padding: 0;
        }
      }

      .btn-group {
        display: flex;
        gap: 1rem;
        margin-left: 70px;

        @media screen and (max-width: 640px) {
          margin-left: 0;
          flex-direction: column;
        }

        .select-all-btn {
          background-color: transparent;
          text-decoration: underline;
          outline: none;
          border: none;
          color: var(--primary);
          text-transform: uppercase;
          font-size: 1.4rem;
          font-style: italic;
          font-family: 'yokomoji';
        }
      }

      .loh-checkbox {
        input[type='checkbox'] {
          display: none;
        }

        input+label {
          display: inline-block;
          width: 52px;
          height: 50px;
          cursor: pointer;
          background-image: url(../../assets/images/common/checkbox-unchecked.png);
          background-size: cover;
        }

        input[type='checkbox']:checked+label {
          background-image: url(../../assets/images/common/checkbox-checked.png);
        }
      }
    }
  }
}

.burn-confirm-modal {
  background-color: var(--primary);
  
  .title {
    font-size: 2.2rem;
    margin-top: 5rem;
    margin-left: 3rem;
    margin-right: 3rem;

    @media screen and (max-width: 640px) {
      font-size: 1.6rem;
      margin-top: 3rem;
    }
  }

  .desc {
    font-size: 1.4rem;
    font-style: bold;
    color: var(--danger);

    @media screen and (max-width: 640px) {
      font-size: 1.1rem;
    }
  }
  
  .status {
    font-size: 1.4rem;
    font-style: bold;
    color: var(--danger);

    @media screen and (max-width: 640px) {
      font-size: 1.1rem;
    }
  }
}

.burning-loh-modal {
  background-color: var(--primary);
  
  .title {
    font-size: 2.2rem;

    @media screen and (max-width: 640px) {
      font-size: 1.6rem;
    }
  }

  .desc {
    font-size: 1.4rem;
    color: var(--danger);

    @media screen and (max-width: 640px) {
      font-size: 1.1rem;
    }
  }
  
  .status {
    font-size: 1.4rem;
    color: var(--danger);

    @media screen and (max-width: 640px) {
      font-size: 1.1rem;
    }
  }
}

.burning-loh-modal {
  padding: 4rem;

  @media screen and (max-width: 640px) {
    padding: 2rem 1rem;
  }
}

.bought-loh-modal {
  padding: 2rem 4rem;

  @media screen and (max-width: 640px) {
    padding: 2rem 1rem;
  }
}